h2 {
  font-weight: 100;
  font-size: 44px;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.moreInfoList {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.moreInfoCard {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 285px;
  height: 222px;
}

.moreInfoItem {
  flex-basis: calc((100% - 60px) / 4);
}

.cardImage {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 20px;
  /* background-image: linear-gradient(
    0deg,
    rgba(235, 208, 208, 0.48) 0%,
    rgba(0, 0, 0, 0.48) 100%
  ); */
}

.cardTitle {
  text-transform: uppercase;
  color: white;
  font-weight: 100;
  z-index: 1;
}

.moreInfoButton {
  margin: 0 auto;
  display: block;
  margin-top: 4rem;
  background-color: #195d08;
}
